import React from "react";
import { useNavigate } from 'react-router-dom';





const ProfitCenterCard = (props) => {
    const navigate = useNavigate();
    
    const cardClick = () => {
        console.log(props.profitCenter);
        navigate('/PCsystems', {state: {profitCenter: props.profitCenter}});
    }

   


    return (
        
        <div className="PCsystemCard" id='cardBody' onClick= {cardClick}>

                <div className="cardName">
                    <p className="forza-light">{props.profitCenter}</p>
                </div>
                <div>
                    <img alt='imagem' src={props.image} />
                </div>
        </div>
    )
}

export default ProfitCenterCard;
