import React from "react";
import { useNavigate } from 'react-router-dom';




const PCsystemCard = (props) => {

    const navigate = useNavigate();
    
    const cardClick = () => {
        console.log(props.profitCenter);
        navigate('/system', {state: {name: props.name}});
    }

    

     

    return (
            
            <div className="PCsystemCard" onClick= {cardClick}>
                    <div className="cardName">
                        <h3 className="forza-medium">{props.name}</h3>
                    </div> 
                    <div>
                         <img className="imgPCSystem" alt='imagem' src={props.image} />
                    </div>
            </div>
            
    )

}

export default PCsystemCard;
