import React from "react";
import { systems } from "./systems";
import { useLocation, useNavigate } from "react-router";
import PCsystemCard from "./PCsystemCard";




const PCsystems = () => {

    const {state} = useLocation();
    const {profitCenter} = state;
    const navigate = useNavigate();

    const filteredSystems = systems.filter((PC) => PC.profitCenter === state.profitCenter);
    console.log(filteredSystems)

   

    const cardComponent = filteredSystems.map((user, i) => {
        return <PCsystemCard name={filteredSystems[i].name} image={filteredSystems[i].image}/>
            
    })

    const goBack = () => {
        navigate(-1);

    }


    return(
        <div>

            <div className="return">
                <img id='returnIcon' onClick= {goBack} src='./images/return.png'/>
            </div>
           
        
            <div className="nameHeader">
           
                <h2 className="forza-black">{state.profitCenter}</h2>
            </div>

           
           

            <section>
            

            
                    {cardComponent}
            


            
            </section>
            
          
        </div>
        )
}

export default PCsystems;