import React from "react";
import ProfitCenterCard from "./ProfitCenterCard";
import { profitCenters } from "./profitCenters";


const CardList = () => {
    const cardComponent = profitCenters.map((user, i) => {
        return <ProfitCenterCard id={profitCenters[i].id} profitCenter={profitCenters[i].profitCenter} image={profitCenters[i].image}/>
    })


    return(
    <section>
        {cardComponent}
    </section>
    );
}

export default CardList;